<template>
  <div>
    <!--loader-->
    <loading :active.sync="isLoading">
      <div class="dot-loader">
        <span class="dot dot1 bg-use"></span>
        <span class="dot dot2 bg-abstract"></span>
        <span class="dot dot3 bg-warning"></span>
        <span class="dot dot4 bg-alert"></span>
      </div>
      <i class="fas fa-fish text-primary me-2 fa-3x"></i>
      <div class="dot-loader">
        <span class="dot dot5 bg-use"></span>
        <span class="dot dot6 bg-abstract"></span>
        <span class="dot dot7 bg-warning"></span>
        <span class="dot dot8 bg-alert"></span>
      </div>
    </loading>
    <div class="row py-4">
      <DisplayBox
        :count="filterToday.length"
        :title="'今日訂單'"
        :bg="'bginfo'"
        @showModal="openTodayModel"
      ></DisplayBox>
      <DisplayBox
        :count="filterWeek.length"
        :title="'本周訂單'"
        :bg="'bgprimary'"
        @showModal="openWeekModel"
      ></DisplayBox>
      <DisplayBox
        :count="filterMonth.length"
        :title="'本月訂單'"
        :bg="'bgsecondary'"
        @showModal="openMonthModel"
      ></DisplayBox>
    </div>
    <div class="row">
      <div class="d-flex justify-content-between">
        <h5 class="text-third">訂單銷售一覽</h5>
        <select
          class="form-select form-select-sm time-select"
          aria-label="select"
          v-model="chartDataType"
        >
          <option selected value="week">本周</option>
          <option value="year">年度</option>
        </select>
      </div>
      <div class="col-md-6">
        <canvas id="weekLineChart" v-show="chartDataType === 'week'"></canvas>
        <canvas id="monthLineChart" v-show="chartDataType === 'year'"></canvas>
      </div>
      <div class="col-md-6">
        <canvas id="weekSalesChart" v-show="chartDataType === 'week'"></canvas>
        <canvas id="MonthSalesChart" v-show="chartDataType === 'year'"></canvas>
      </div>
    </div>
    <!-- Modaltoday -->
    <div
      class="modal fade"
      id="DisplayBoxModal"
      tabindex="-1"
      aria-labelledby="DisplayBoxModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-xl  modal-dialog-scrollable">
        <div class="modal-content">
          <div class="modal-header bg-primary text-third">
            <h5 class="modal-title" id="DisplayBoxModalLabel">
              {{ ModalTitle }}
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <table
              class="table table-striped"
              v-if="currentFilterDay.length > 0"
            >
              <thead>
                <tr>
                  <th>購買時間</th>
                  <th>購買內容</th>
                  <th>應付金額</th>
                  <th>狀態</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="item in currentFilterDay" :key="item.id">
                  <td>{{ TrnasformDate(item.create_at) }}</td>
                  <td>
                    <ul class="pl-0">
                      <li v-for="product in item.products" :key="product.id">
                        {{ product.product.title }} {{ product.qty
                        }}{{ product.product.unit }}
                      </li>
                    </ul>
                  </td>
                  <td>{{ item.total | currency }}</td>
                  <td>
                    <span class="text-success" v-if="item.is_paid">已付款</span
                    ><span v-else class="text-danger">未付款</span>
                  </td>
                </tr>
              </tbody>
            </table>
            <div v-else>尚無訂單</div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-primary text-third"
              data-bs-dismiss="modal"
            >
              關閉
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DisplayBox from "../displayBox.vue";
import { mapGetters } from "vuex";
import moment from "moment";
import { Chart, registerables } from "chart.js";
Chart.register(...registerables);
export default {
  name: "dashboardIndex",

  data() {
    return {
      ordersAll: [],
      chartOrders: [],
      chartTotals: [],
      chartLabels: [],
      chartDataType: "week",
      DisplayBoxModal: null,
      ModalTitle: "",
      currentFilterDay: [],
    };
  },
  components: {
    DisplayBox,
  },
  computed: {
    ...mapGetters(["isLoading", "checkoutVisible"]),
    filterToday() {
      let items = [];
      const today = new Date();
      let todayStr = `${today.getFullYear()}/${today.getMonth() +
        1}/${today.getDate()}`;
      items = this.ordersAll.filter((item) => {
        const date = new Date(item.create_at * 1000);
        let dateStr = `${date.getFullYear()}/${date.getMonth() +
          1}/${date.getDate()}`;

        return todayStr === dateStr;
      });

      return items;
    },
    filterWeek() {
      let items = [];
      const fromDate = moment().startOf("week");
      const toDate = moment().endOf("week");
      items = this.ordersAll.filter((item) => {
        return moment(item.create_at * 1000).isBetween(fromDate, toDate);
      });
      return items;
    },
    filterMonth() {
      let items = [];
      const fromDate = moment().startOf("month");
      const toDate = moment().endOf("month");
      items = this.ordersAll.filter((item) => {
        return moment(item.create_at * 1000).isBetween(fromDate, toDate);
      });
      return items;
    },
  },
  methods: {
    TrnasformDate(timestamp) {
      const date = new Date(timestamp * 1000);
      let dateStr = `${date.getFullYear()}/${date.getMonth() +
        1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}`;
      return dateStr;
    },
    getOrderAll() {
      let page = 1;
      let totalPage = 1;
      let items = [];
      const api = `${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/orders?page=${page}`;
      const vm = this;
      this.$store.dispatch("updatedLoading", true);
      this.$http
        .get(api)
        .then((res) => {
          if (res.data.success) {
            items = res.data.orders;
            totalPage = res.data.pagination.total_pages;

            for (let i = 2; i < totalPage + 1; i++) {
              let apiurl = `${process.env.VUE_APP_APIHOST}/api/${process.env.VUE_APP_APIPATH}/admin/orders?page=${i}`;
              this.$http
                .get(apiurl)
                .then((res) => {
                  if (res.data.success) {
                    vm.ordersAll = items.concat(res.data.orders);
                    vm.getWeekOrder();
                    vm.setMonthsChart();
                  } else {
                    vm.$store.dispatch("updateMessage", {
                      message: res.data.message,
                      status: "danger",
                    });
                  }
                  this.$store.dispatch("updatedLoading", false);
                })
                .catch((err) => {
                  console.log(err);
                });
            }
          } else {
            vm.$store.dispatch("updateMessage", {
              message: res.data.message,
              status: "danger",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWeekOrder() {
      const fromDate = moment().startOf("week");
      const toDate = moment().endOf("week");
      this.chartOrders = this.ordersAll.filter((item) => {
        return moment(item.create_at * 1000).isBetween(fromDate, toDate);
      });
      this.setWeekChart();
    },

    setWeekChart() {
      const fromDate = moment().startOf("week");
      const toDate = moment().endOf("week");
      let day7 = moment(fromDate, ["YYYY/MM/DD"]);
      let day1 = moment(fromDate)
        .add(1, "days")
        .format("YYYY/MM/DD");
      let day2 = moment(fromDate)
        .add(2, "days")
        .format("YYYY/MM/DD");
      let day3 = moment(fromDate)
        .add(3, "days")
        .format("YYYY/MM/DD");
      let day4 = moment(fromDate)
        .add(4, "days")
        .format("YYYY/MM/DD");
      let day5 = moment(fromDate)
        .add(5, "days")
        .format("YYYY/MM/DD");
      let day6 = moment(toDate, ["YYYY/MM/DD"]);

      let daylen7 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");

        return date == day7;
      });
      let daylen1 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");
        return date == day1;
      });
      let daylen2 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");
        return date == day2;
      });
      let daylen3 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");
        return date == day3;
      });
      let daylen4 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");
        return date == day4;
      });
      let daylen5 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");
        return date == day5;
      });
      let daylen6 = this.chartOrders.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM/DD");

        return date == day6;
      });
      this.chartLabels = [
        "週日",
        "周一",
        "週二",
        "週三",
        "週四",
        "週五",
        "週六",
      ];
      this.chartTotals = [
        daylen7.length,
        daylen1.length,
        daylen2.length,
        daylen3.length,
        daylen4.length,
        daylen5.length,
        daylen6.length,
      ];

      this.setChart(
        "weekLineChart",
        "bar",
        "銷售量",
        "rgba(69, 120, 252, 0.6)",
        "rgba(69, 120, 252, 0.7)"
      );
      //銷售額
      let day7Sale = 0;
      let day1Sale = 0;
      let day2Sale = 0;
      let day3Sale = 0;
      let day4Sale = 0;
      let day5Sale = 0;
      let day6Sale = 0;
      daylen7.forEach((item) => {
        day7Sale += item.total;
      });
      daylen1.forEach((item) => {
        day1Sale += item.total;
      });
      daylen2.forEach((item) => {
        day2Sale += item.total;
      });
      daylen3.forEach((item) => {
        day3Sale += item.total;
      });
      daylen4.forEach((item) => {
        day4Sale += item.total;
      });
      daylen5.forEach((item) => {
        day5Sale += item.total;
      });
      daylen6.forEach((item) => {
        day6Sale += item.total;
      });
      this.chartTotals = [
        day7Sale,
        day1Sale,
        day2Sale,
        day3Sale,
        day4Sale,
        day5Sale,
        day6Sale,
      ];
      this.setChart(
        "weekSalesChart",
        "line",
        "銷售額",
        "rgba(63, 191, 127, 0.6)",
        "rgba(63, 191, 127, 0.7)"
      );
    },
    setMonthsChart() {
      let JanQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/01`;
      });
      let FebQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/02`;
      });
      let MarQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/03`;
      });
      let AprQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");

        return date === `${moment().year()}/04`;
      });
      let MayQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/05`;
      });
      let JunQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/06`;
      });
      let JulQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/07`;
      });
      let AugQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/08`;
      });
      let SepQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/09`;
      });
      let OctQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/10`;
      });
      let NovQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/11`;
      });
      let DecQty = this.ordersAll.filter((item) => {
        let date = moment(item.create_at * 1000).format("YYYY/MM");
        return date === `${moment().year()}/12`;
      });

      this.chartLabels = [
        "一月",
        "二月",
        "三月",
        "四月",
        "五月",
        "六月",
        "七月",
        "八月",
        "九月",
        "十月",
        "十一月",
        "十二月",
      ];
      this.chartTotals = [
        JanQty.length,
        FebQty.length,
        MarQty.length,
        AprQty.length,
        MayQty.length,
        JunQty.length,
        JulQty.length,
        AugQty.length,
        SepQty.length,
        OctQty.length,
        NovQty.length,
        DecQty.length,
      ];
      this.setChart(
        "monthLineChart",
        "bar",
        "銷售量",
        "rgba(247, 142, 38, 0.6)",
        "rgba(247, 142, 38, 0.7)"
      );
      let sale7 = 0;
      let sale1 = 0;
      let sale2 = 0;
      let sale3 = 0;
      let sale4 = 0;
      let sale5 = 0;
      let sale6 = 0;
      let sale8 = 0;
      let sale9 = 0;
      let sale10 = 0;
      let sale11 = 0;
      let sale12 = 0;
      JanQty.forEach((item) => {
        sale1 += item.total;
      });
      FebQty.forEach((item) => {
        sale2 += item.total;
      });
      MarQty.forEach((item) => {
        sale3 += item.total;
      });
      AprQty.forEach((item) => {
        sale4 += item.total;
      });
      MayQty.forEach((item) => {
        sale5 += item.total;
      });
      JunQty.forEach((item) => {
        sale6 += item.total;
      });
      JulQty.forEach((item) => {
        sale7 += item.total;
      });
      AugQty.forEach((item) => {
        sale8 += item.total;
      });
      SepQty.forEach((item) => {
        sale9 += item.total;
      });
      OctQty.forEach((item) => {
        sale10 += item.total;
      });
      NovQty.forEach((item) => {
        sale11 += item.total;
      });
      DecQty.forEach((item) => {
        sale12 += item.total;
      });
      this.chartTotals = [
        sale1,
        sale2,
        sale3,
        sale4,
        sale5,
        sale6,
        sale7,
        sale8,
        sale9,
        sale10,
        sale11,
        sale12,
      ];
      this.setChart(
        "MonthSalesChart",
        "line",
        "銷售額",
        "rgba(127, 63, 191, 0.6)",
        "rgba(127, 63, 191, 0.7)"
      );
    },
    setChart(chartName, type, title, bgColor, borderColor) {
      //set Chart
      let ctx = document.getElementById(chartName).getContext("2d");
      let config = {
        type: type,
        data: {
          labels: this.chartLabels,
          datasets: [
            {
              label: title,
              backgroundColor: bgColor, //'',
              borderColor: borderColor, //'rgba(69, 120, 252, 0.7)',
              data: this.chartTotals,
              fill: false,
            },
          ],
        },
        options: {
          responsive: true,
        },
      };
      new Chart(ctx, config);
    },
    openTodayModel() {
      this.currentFilterDay = Array.from(this.filterToday);
      this.ModalTitle = "本日訂單";
      this.DisplayBoxModal.show();
    },
    openWeekModel() {
      this.currentFilterDay = Array.from(this.filterWeek);
      this.ModalTitle = "本周訂單";
      this.DisplayBoxModal.show();
    },
    openMonthModel() {
      this.currentFilterDay = Array.from(this.filterMonth);
      this.ModalTitle = "本月訂單";
      this.DisplayBoxModal.show();
    },
  },
  created() {
    this.getOrderAll();
    moment().format();
    this.$store.dispatch("updateCheckoutBtn", true);
  },
  mounted() {
    this.DisplayBoxModal = new bootstrap.Modal(
      document.getElementById("DisplayBoxModal")
    );
  },
};
</script>
